@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');

body {
  background: black;
  overflow-x:hidden;
  font-family:'VT323', monospace;
}

.cover {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  font-size:50px;
  width: 1200px;
  margin: 10px;
  justify-content: center;
  align-items:center;
  text-align: center;
  z-index: 50;
  color: rgb(241, 233, 241);
  font-family: 'VT323', monospace;
  background-color: rgba(0, 0, 0, .6);
}

.cover.shown {
  display: flex;
  flex-direction: column;
}

#start-game .start-div {
  color: rgb(226, 226, 151);
}

p{
  text-align: center;
  width: 110px;
  font-size: 18px;
  line-height: 20px;
}

.main-bg {
  border-radius: 5px;
  width: 1200px;
  height: 680px;
}

.castle-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 100px;
  padding-right: 120px;
  font-size:22px;
}

.castle-life{
  margin-bottom: 5px;
  padding: 5px;
  font-size: 24px;
  font-weight: bold;
  background-color: rgb(177, 9, 37);
  display: flex;
  border-radius: 3px;
  align-content: center;
}

.personal-links{
  margin-bottom: 5px;
  margin-left: 20px;
  display: flex;
  justify-content: center;
  align-content: center;
}

.personal {
  padding-left: 10px;
  width: 32px;
  height: 32px;
  opacity: 65%;
}

.castle-game-container{
  color:white;
  position: fixed;  
  justify-content: center;
  top: 180px;
  left: 55px;
  width: 1100px;
  height: 500px;
  z-index: 10px;
}

.castle-life {
  display: flex;
  justify-content: space-between;
  width: 60px;
}

.castle-board{
  padding-left: 18px;
  display: grid;
  grid-template-columns: repeat(5, auto);
  row-gap: 10px;
}

.room {
  position:relative;
  width: 200px;
  height: 143px;
  background-color: rgb(44, 1, 1);
  opacity: 85%;
  border-radius: 5px;
  margin:0px;
}

.room-design{
  position: absolute;
  display:flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: transform 1000ms ease-in-out;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.door-img{
  width: 200px;
  height: 145px;
  border-radius: 10px;
}

#shown .door-side {
  transform: rotateY(-180deg);
}

.door-side {
  transform: rotateY(0deg)
}

#shown .suit-side {
  transform: rotateY(0deg);
}

.suit-side {
  transform: rotateY(180deg);
}

/////////////////////////////////////

.canvas-container {
  position: absolute;
  top: 380px;
  left: 620px;
  transform: translate(-50%, -50%);
}

.c-message{
  text-align: center;
}

.canvas-success{
  width: 290px;
  height: 40px;
  position: absolute;
  top: 350px;
  left: 620px;
  transform: translate(-50%, -50%);
  z-index:25;
  font-size: 19px;
  padding: 5px;
  font-weight: bold;
  font-family: 'VT323', monospace;
  color: rgb(241, 237, 234);
  border-radius: 3px;
  background-color: rgb(64, 124, 64);
  opacity: 90%;
  display: none;
}

.canvas-fail{
  width: 290px;
  height: 40px;
  position: absolute;
  top: 350px;
  left: 620px;
  transform: translate(-50%, -50%);
  z-index:25;
  font-size: 19px;
  padding: 5px;
  font-weight: bold;
  font-family: 'VT323', monospace;
  color: rgb(253, 251, 250);
  border-radius: 3px;
  background-color: rgb(161, 82, 96);
  opacity: 90%;
  display: none;
}

.canvas-message-container{
  width: 290px;
  height: 60px;
  margin-bottom: 5px;
  padding: 5px;
  justify-content: center;
  align-items: center;
  align-content: center;
  font-size: 19px;
  font-weight: bold;
  font-family: 'VT323', monospace;
  color: rgb(241, 237, 234);
  border-radius: 5px;
  background-color: rgb(2, 30, 37);
  text-align: center;
  display: none;
}


.canvas-mini-game{
  border-radius: 5px;
  display: none;
}




